.App-body{
  padding-top: 90px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(2px + 2vmin);
  color: rgb(37, 37, 37);
}

table {
  width:100%;
}

table, th, td {
  border: 1px solid rgb(156, 155, 155);
  border-collapse: collapse;
}

th, td {
  padding: 14px;
  text-align: left;
}

/* tr:nth-child(even) {
  background: #eee;
  background: linear-gradient(0deg, rgba(0, 0, 200, 0.2), rgba(0, 0, 200, 0));
}

tr:nth-child(odd) {
 background-color: rgb(49, 49, 49);
} */

tr {
  /* background: linear-gradient(90deg, rgba(53, 21, 21, 0.3), rgba(0, 0, 200, 0)); */
  background-color:rgba(24, 21, 53, 0.4);
  font-family:'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
  font-weight: 200;
}

th {
  background-color: rgba(21, 53, 21, 0.3);
  color: rgb(51, 50, 50);
}

.dashboard {
  padding-top: 30px;
  width: 100%;
  height: 100%;
  margin: 0;
  font-family: "Lato", sans-serif;
  font-size: large;
}

.dashboardDropdown {
  margin-top: 50px;
  text-align: center;
  box-shadow:4px 4px 10px rgba(0,0,0,0.30);
  border-radius: 5px;
  background-color: rgba(235, 241, 245, 0.8);
  font-size: large;
  padding: 10px;
}

.download {
  margin-top: 120px;
  text-align: center;
  box-shadow:4px 4px 10px rgba(0,0,0,0.30);
  border-radius: 5px;
  background-color: rgba(228, 240, 247, 0.7);
  font-size: large;
  padding: 20px;
}

#location {
  width: 180px;
  font-size: medium;
}

iframe {
  display: block;
  margin: auto;
  top: 50px;
}

.navigation {
  float: right;
  position: absolute;
  top: 18px;
  right: 20px;
  z-index: 1;
}

ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
}

li {
  display: inline;
}

li a {
  color: black;
  text-align: center;
  padding: 14px 16px;
  text-decoration: none;
}

/* Change the link color on hover */
li a:hover {
  background-color:rgba(255,255,255,0.5);
}

form, label {
  margin: 0 auto;
  text-align: center;
  color: black;
  padding-left: 30px;
  padding-right: 30px;
}

label {
  line-height: 50px;
}

button {
  margin: auto;
  display: block;
}

p {
  color: black;
  margin: 0 auto;
  text-align: center;
}

.about {
  margin: 0;
  padding: 0;
}

.aboutContainer {
  width: 191.9vh;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 75vh;
  margin: 0 auto;
  background: #3A1C71;  /* fallback for old browsers */
  background: -webkit-linear-gradient(to right, #FFAF7B, #D76D77, #3A1C71);  /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to right, #FFAF7B, #D76D77, #3A1C71); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}

.aboutHeader {
  font-family: 'Montserrat', sans-serif;
  color: white;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  padding: 80px;
}

.aboutHeader h1 {
  font-size: 80px;
  font-weight: 900;
}

.aboutHeader h3 {
  font-size: 24px;
  font-weight: 300;
  letter-spacing: 2.75px;
}

.bottomContainer {
  background-color: beige;
  padding-top: 25px;
  color: black;
  text-align: center;
}

.bottomContainer p {
  padding-top: 15px;
  font-size: large;
  font-weight: 600;
  text-align: left;
  letter-spacing: 2px;
  line-height: 200%;
  padding-left: 150px;
  padding-right: 150px;
}

.arrow {
  box-sizing: border-box;
  height: 5vw;
  width: 5vw;
  border-style: solid;
  border-color: white;
  border-width: 0px 2px 2px 0px;
  transform: rotate(45deg);
  transition: border-width 150ms ease-in-out;
}

.arrow:hover {
  border-bottom-width: 4px;
  border-right-width: 4px;
}

.NoMatchPage {
  padding-top: 140%;
  color: red;
}